* {
  box-sizing: border-box
  
} 
body {
    font: 4vmin "Bitter", Futura, sans-serif;
    margin: 20px;
    text-align: center;
    background-image: url("detective.jpg");
  }
h1{
  margin-top: 2vmin;
  margin-bottom: 1vmin;
  text-align: center;
}

button{
  margin:auto;
  width: 50vmin;
  height: 8vmin;
  border-radius: 5px;
  font-size: 4vmin;
  color: white;
  border: none;
  background-color: #f44336;
}

form{
  margin: auto  ;
  width: 50vmin;
  text-align: center;
}

form >label>*{
  
  height: 8vmin;
  border: lightgray;
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
  margin: auto;
  margin-top: 1vh;
  margin-bottom: 1vh;
  width: 50vmin;
  font-size: 4vmin;
  text-align: center;
  
}

.content{
  height: 8vmin;
  border: lightgray;
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
  margin: auto;
  margin-top: 1vh;
  margin-bottom: 1vh;
  width: 50vmin;
  font-size: 4vmin;
  text-align: center;
}

#container{
  width: 70vw;  
  height: 100%;
  padding: 5vh;
  margin: auto;
  background-color: white;
}

label{
  

  height:8vmin;
}

p{
  margin: 5px;
  padding: 0;
  font-size: 3vmin;
}